import TsuroImg from '../assets/tsuro.jpg';
import PetCareImg from '../assets/petcare.jpg';
import QuandomImg from '../assets/quandom.jpg';
import Footer from './Footer';

const styles = {
    divRow: {
        height: 800,
        fontFamily: "'Voltaire', 'sans-serif'",
        color: 'white',
        letterSpacing: '2px',
        fontSize: '160%',
        backgroundColor: 'rgba(0,0,0,0.1)',
        position: 'relative',
        display: 'flex',
        justifyContent: 'space-evenly'
    },
    aLink: {
        textDecoration: 'none',
    },
    container: {
        display: 'flex',
        height: '100%',
        verticalAlign: 'center',
        flex: 1,
        alignItems: 'baseline',
        flexDirection: 'column',
        justifyContent: 'space-evenly',
    },
    cardHeader: {
        textAlign: 'center',
        fontSize: '120%',
        flex: 1,
        zIndex: 2,
        backgroundColor: 'rgba(0,0,0,0.4)',
        color: 'white',
        // marginBottom: '10px',
        fontWeight: 'bold',
    },
    rowDiv: {
        alignItems: 'center',
        flex: 1,
    },
    cardText: {
        fontSize: '60%',
        fontWeight: 'normal',
        textAlign: 'left',
    },
    cardTechText: {
        fontSize: '50%',
        fontWeight: 'normal',
    },
    chip: {
        fontFamily: "'Voltaire', 'sans-serif'",
        color: 'white',
        backgroundColor: 'rgba(0,0,0,0.6)',
    },
    logoImg: {
        borderRadius: '15px',
        padding: '10px',
    }
}

function Project(props) {
    let { imageOnLeft, backgroundImage, projectImage, projectLink, projectName, projectDesc, projectTech } = props;
    let left, right;
    if (imageOnLeft) {
        left =  (<Col>
                    <Paper elevation={5} style={styles.cardHeader}>
                        <Image src={projectImage} fluid style={styles.logoImg}/>
                    </Paper>
                </Col>);
        right = (null);
    } else {
        right =  (<Col>
                    <Paper elevation={5} style={styles.cardHeader}>
                        <Image src={projectImage} fluid style={styles.logoImg}/>
                    </Paper>
                </Col>);
        left = (null);
    }
    return (
        <Parallax bgImage={backgroundImage} strength={500} blur={3}>
            <div style={styles.divRow}>
                <Container style={styles.container}>
                    <Row style={styles.rowDiv}>
                        {left}
                        <Col>
                            <a href={projectLink} style={styles.aLink}>
                                <Card className="text-center" style={styles.cardHeader}>
                                    <Card.Header style={styles.cardDeveloper}>
                                        {projectName}
                                    </Card.Header>
                                    <Card.Body>
                                        <Card.Text style={styles.cardText}>
                                            {projectDesc}
                                        </Card.Text>
                                        <Card.Text style={styles.cardTechText}>
                                            {projectTech.map((x) => {return (<Chip style={styles.chip} size='small' label={x}/>);})}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>
                            </a>
                        </Col>
                        {right}
                    </Row>
                </Container>
            </div>
        </Parallax>        
    );
};

const projectList = [
    {
        imageOnLeft: true,
        backgroundImage: div5Background,
        projectImage: websiteImg,
        projectLink: '#',
        projectName: 'Personal Website',
        projectDesc: 'Portfolio website made to showcase my projects, videos, pictures and contact information.',
        projectTech: ['react', 'react-parallax', 'react-router-dom', 'material-ui', 'webfontloader', 'react-bootstrap', 'apache2', 'javascript'],
    },
    {
        imageOnLeft: false,
        backgroundImage: div4Background,
        projectImage: TsuroImg,
        projectLink: '#',
        projectName: 'Tsuro: the TCP version',
        projectDesc: 'Recreated the popular tile-based board game, on a virtual board made using Python, where the referee and players communicate via a TCP socket. The game includes various AI models as well as the option to play with others.',
        projectTech: ['python', 'socket', 'pickle', 'logging', 'tkinter', 'software-development'],
    },
    {
        imageOnLeft: true,
        backgroundImage: div3Background,
        projectImage: PetCareImg,
        projectLink: '#',
        projectName: 'PetCare: the Pet WebApp',
        projectDesc: 'A simple React web app model to prototype a one-stop application for all pet related needs. Developed three modules with functions ranging from scheduling and sharing walks with connected members to looking up food toxicities before feeding pets.',
        projectTech: ['react', 'react-bootstrap', 'heroku', 'human-computer-interaction'],
    },
    {
        imageOnLeft: false,
        backgroundImage: div2Background,
        projectImage: QuandomImg,
        projectLink: '#',
        projectName: 'Quandom: the Mobile Quiz App',
        projectDesc: 'A local co-op trivia game made for android phones. Pulls random questions from a quiz API for 1-4 players to play. Built using a simple material UI design and minimal setup.',
        projectTech: ['java', 'gradle', 'android-studio', 'play-store', 'mobile-application-development'],
    },
];

function Projects() {
    return (
        <div>
            <Topbar />
            {projectList.map((x) => {return Project(x);})}
            <Footer />
        </div>
    );
}

export default Projects;