function TitleCard(CardText, CardLink = "#") {
    const imageClick = () => {
        console.log('Click');
    };
    if (CardLink !==  '#') {
        CardLink = '#' + CardLink;
    }
    return (
        <a href={CardLink} style={styles.aLink}>
            <Card className="text-center" style={styles.cardHeader} onClick={() => imageClick()}>
                <Card.Body style={styles.cardBody}>
                    <Card.Text style={styles.cardTextOld}>
                        {CardText}
                    </Card.Text>
                </Card.Body>
            </Card>
        </a>
    );
};

